.profile-picture-float {
	float: right;
	margin-left: 1rem;
	margin-bottom: 1rem;
	@media screen and (max-width: $viewport-small){
		float: none;
		margin-left: 0rem;
		margin-top: 1rem;
	}
}

.header-name {
	font-size: $site-title-font-size;
	font-weight: bold;
}

.header-container {
	display: flex;
	flex-flow: row wrap;
}

.header-flex-gap {
	min-width: 2em;
	flex: auto;
}

.header-picture {
	width: 220px;
	@media screen and (max-width: $viewport-medium){
		width: 200px;
		max-width: 100%;
	}
	@media screen and (max-width: $viewport-small){
		display: none;
	}
}

.new-tag {
	background-color: #f5e042;
	color: $dark-gray;
	margin-right: 0.25rem;
	padding: 0 0.25rem;
}

.collapse {
  display: block;
  max-height: 0px;
  overflow: hidden;
  transition: max-height .5s cubic-bezier(0, 1, 0, 1);
	&.show {
    max-height: 99em;
    transition: max-height .5s ease-in-out;
  }
}

body {
	overflow-y: scroll;	
}